		function checkScrollToCircles() {

			let circle1 = $('.circle-1');
			let offset = $(circle1).offset().top + 100;

			if($(window).scrollTop() + $(window).height() >= offset) {
				$('.circle-1').addClass('animate-show');
				let startTimer = 400;
				$('.circle:not(.circle-1)').each(function($key,$item) {
					setTimeout(() => {
						$($item).addClass('animate-show');
					}, startTimer);
					startTimer = startTimer + 300;
				});

			}

		}

		// scroll scale big bubbles

		window.addEventListener('scroll', checkScrollToCircles, false);



		// bubbles

		function bubbles(el) {
			let $bubbles = el;
			const min_bubble_count = 20;
			const max_bubble_count = 30;
			const min_bubble_size = 7;
			const max_bubble_size = 24;
  		let bubbleCount = min_bubble_count + Math.floor(Math.random() * (max_bubble_count + 1));

		  for (let i = 0; i < bubbleCount; i++) {

				let container = document.createElement('div');

				container.className = 'bubble-container';

				let bubble = document.createElement('div');

				bubble.className = 'bubble';

				$(container).append($(bubble));

		    $bubbles.append(container);
		  }

		  $($bubbles).find('.bubble-container').each(function(){
		    let pos_rand = Math.floor(Math.random() * 101);

		    let size_rand = min_bubble_size + Math.floor(Math.random() * (max_bubble_size + 1));

		    let delay_rand = Math.floor(Math.random() * 16);

		    let speed_rand = 10 + Math.floor(Math.random() * 9);

		    let blur_rand = Math.floor(Math.random() * 1);

		    let $this = $(this);
		    $this.css({
		      'left' : pos_rand + '%',
		      '-webkit-animation-duration' : speed_rand + 's',
		      '-moz-animation-duration' : speed_rand + 's',
		      '-ms-animation-duration' : speed_rand + 's',
		      'animation-duration' : speed_rand + 's',
		      '-webkit-animation-delay' : delay_rand + 's',
		      '-moz-animation-delay' : delay_rand + 's',
		      '-ms-animation-delay' : delay_rand + 's',
		      'animation-delay' : delay_rand + 's',
		      '-webkit-filter' : 'blur(' + blur_rand  + 'px)',
		      '-moz-filter' : 'blur(' + blur_rand  + 'px)',
		      '-ms-filter' : 'blur(' + blur_rand  + 'px)',
		      'filter' : 'blur(' + blur_rand  + 'px)',
		    });

		    $this.children('.bubble').css({
		      'width' : size_rand + 'px',
		      'height' : size_rand + 'px'
		    });

		  });
}


$(document).ready(function() {
	bubbles(document.getElementsByClassName('bubbles-1')[0]);
	bubbles(document.getElementsByClassName('bubbles-2')[0]);
});


</script>
