<template>
  <div id="app">


    <router-view class="page"></router-view>
    <Footer></Footer>
    <notifications group="app"></notifications>
  </div>
</template>

<script>
// @ is an alias to /src
import Footer from "@/components/Footer.vue";
import Menu from "@/components/Menu.vue";
import Authorization from "@/components/Authorization.vue";
import {mapActions, mapGetters, mapMutations} from 'vuex';

export default {
  name: "App",
  components: {
    Footer,
    Menu,
    Authorization
  },
  methods: {
    ...mapActions(['GET_SESSION_INFO']),
    ...mapMutations(['RESET_USER']),
    async init() {
      await this.GET_SESSION_INFO();
      if (!this.sessionInfo.isAuthenticated && this.$route.name === 'account') {
        this.RESET_USER();
        await this.$router.push('/');
      } else if (!this.sessionInfo.isAuthenticated) {
        this.RESET_USER();
      }
    }
  },
  computed: {
    ...mapGetters({
      sessionInfo: 'getSessionInfo'
    })
  },
  mounted() {
    this.init();
  }
};





</script>

<style lang="sass">


body
  height: auto
@font-face
  font-family: "Avenir Regular"
  src: url(../src/assets/fonts/AvenirNextCyr-Regular.ttf)
@font-face
  font-family: "Avenir Bold"
  src: url(../src/assets/fonts/AvenirNextCyr-Bold.ttf)
#app
  max-width: 100%
  overflow: hidden
</style>
