<template>
  <div class="registration">
    <h2 class="title">Create an account</h2>
    <social-auth></social-auth>
    <form class="registration__form" @submit.prevent="registration">

      <!-- Loading Overlay -->
      <loading :active.sync="isLoading"
               :is-full-page="false"
               color="#0060f1"
               loader="dots">
      </loading>

      <!-- name -->
      <v-input  name="Username"
                type="text"
                v-model="user.userName"
                @change="updateUserName"
                :isError="$v.user.userName.$error">
        <template slot="error">
          <div class="error" v-if="!$v.user.userName.required">Field is required</div>
          <div class="error" v-else-if="!$v.user.userName.checkUserName">{{$v.user.userName.$params.checkUserName}}</div>
        </template>
      </v-input>

      <!-- email -->
      <v-input  name="Email"
                type="email"
                v-model="user.email"
                @change="updateUserEmail"
                :isError="$v.user.email.$error">
        <template slot="error">
          <div class="error" v-if="!$v.user.email.required">
            Email is required
          </div>
          <div class="error" v-if="!$v.user.email.email">
            Incorrect Email format
          </div>
          <div class="error" v-else-if="!$v.user.email.checkEmail">
            {{$v.user.email.$params.checkEmail}}
          </div>
        </template>
      </v-input>

      <!-- pass -->
      <v-input  name="Password"
                :type="typePassword"
                v-model="user.password"
                @change="updateUserPassword"
                :isError="$v.user.password.$error">
        <template slot="optional">
          <button class="btn btn--empty" type="button" name="showPass" @click="showPass = !showPass">
            <img width="20" src="@/assets/img/svg/eye.svg" alt="eye" v-if="showPass">
            <img width="20" src="@/assets/img/svg/hide.svg" alt="eye" v-else>
          </button>
        </template>
        <template slot="error">
          <div class="error" v-if="!$v.user.password.required">Password is required</div>
          <div class="error" v-else-if="!$v.user.password.regExPass">Your password is too simple</div>
          <div class="error" v-else-if="!$v.user.password.minLength">Password must have at least {{$v.user.password.$params.minLength.min}} characters</div>
          <div class="error" v-else-if="!$v.user.password.maxLength">Password must have no more {{$v.user.password.$params.maxLength.max}} characters</div>
        </template>
      </v-input>

      <!-- password meter -->
      <password-meter :pass="user.password"></password-meter>

      <!--confirm pass -->
      <v-input  name="Confirm password"
                :type="typePassword"
                v-model="user.confirmPassword"
                @change="updateUserConfirmPassword"
                :isError="$v.user.confirmPassword.$error">
        <template slot="optional">
          <button class="btn btn--empty" type="button" name="showPass" @click="showPass = !showPass">
            <img width="20" src="@/assets/img/svg/eye.svg" alt="eye" v-if="showPass">
            <img width="20" src="@/assets/img/svg/hide.svg" alt="eye" v-else>
          </button>
        </template>
        <template slot="error">
          <div class="error" v-if="!$v.user.confirmPassword.sameAsPassword">Passwords must be identical.</div>
        </template>
      </v-input>
      
        <!--  Country -->
      <multiselect class="countryRow__country"
                   @input="updateUserCountry"
                   :value="countries[countryIndex]"
                   placeholder="Select Country"
                   label="name"
                   track-by="name"
                   :options="countries"
                   :option-height="104"
                   :show-labels="true"
                   >
        <template slot="singleLabel" slot-scope="props" class="select-line">
          <country-flag :country="props.option.code" size='small'/>
          {{props.option.name}}
        </template>
    
        <template slot="option" slot-scope="props" class="select-line">
          <country-flag :country="props.option.code" size='small'/>
          {{props.option.name}}
        </template>
      </multiselect>

      <div class="birth">
        <div class="blackTitle">Date of Birth</div>
        <div class="dateRow">
          <multiselect class="dateRow__day"
                       :show-labels="false"
                       @input="updateBirthDate('birthDay', $event)"
                       :value="bDay.toString().padStart(2, '0')"
                       :options="daysTab"
          >
          </multiselect>
          <multiselect class="dateRow__month"
                       :show-labels="false"
                       @input="updateBirthDate('birthMonth', $event)"
                       :value="bMonth.toString().padStart(2, '0')"
                       :options="monthTab"
          >
          </multiselect>
          <multiselect class="dateRow__year"
                       :show-labels="false"
                       @input="updateBirthDate('birthYear', $event)"
                       :value="bYear"
                       :options="yearsTab"
          >
          </multiselect>
        </div>
      </div>
      
      
      <!-- currency -->
      <Dropdown class="dropdown--input"
                :options="currencies"
                :label="true"
                placeholder="Currency"
                v-model="user.currency"
                @change="updateCurrency"
                :isError="$v.user.currency.name.$error" v-slot>
        <div class="error" v-if="!$v.user.currency.name.currency">Currency is required</div>
      </Dropdown>

      <!-- T&C -->
      <div class="checkbox" :class="{'checkbox--error' : $v.user.acceptTC.$error}">
        <input class="checkbox__input" id="terms-of-use" type="checkbox" v-model="user.acceptTC">
        <label class="checkbox__label" for="terms-of-use">
          <span class="checkbox__text">
            I accept the <a class="link" href="#">Terms &amp; Conditions and Privacy policy</a>
          </span>
        </label>
        <div class="checkbox__error">
          <div class="error" v-if="!$v.user.acceptTC.checked">
            Terms &amp; Conditions and Privacy policy consent must be granted
          </div>
        </div>
      </div>

      <!-- btn -->
      <button class="btn btn--with-btn" type="submit" name="Create an account">
        Create an account
        <span class="btn-in-btn">
          <img src="@/assets/img/svg/triangle.svg" alt="Arrow">
        </span>
      </button>
    </form>
  </div>
</template>

<script>
import SocialAuth from '@/components/Authorization/SocialAuth';
import Input from '@/components/Input';
import Dropdown from '@/components/Dropdown';
import PasswordMeter from '@/components/Authorization/PasswordMeter';
import call from '@/services/callService';
import {mapMutations} from 'vuex';
import {helpers, required, sameAs, email, minLength, maxLength} from 'vuelidate/lib/validators';
const touchMap = new WeakMap();
import Multiselect from 'vue-multiselect';
import CountryFlag from 'vue-country-flag';
let regExPass = helpers.regex('alpha', /(?=.*\d+)(?=.*[A-Za-z]+).{8,20}/);

export default {
  name: 'reg',
  data() {
    return {
      user: {
        userName: '',
        email: '',
        password: '',
        confirmPassword: '',
        country: '',
        currency: {
          name: undefined
        },
        birthDate: "1970-12-30",
        acceptTC: true
      },
      bDay: '',
      bMonth: '',
      bYear: '',
      showPass: false,
      isLoading: false,
      userIpCountry: '',
      countries: [],
      currencies: [],
      activationUrl: (process.env.NODE_ENV === 'development') ? 'http://localhost:8080/#/activation&key=' : `${window.location.protocol}//${window.location.hostname}/activation&key=`
    }
  },
  validations: {
    user: {
      userName: {
        required,
        async checkUserName(value) {
          try {
            let result = await call.validateUsername(value);
            if (result.kwargs.isAvailable) {
              return true;
            } else {
              this.$v.user.userName.$params.checkUserName = result.kwargs.error;
              return false;
            }
          }
          catch(err) {
            const message = err.kwargs.desc || JSON.stringify(err);
            throw new Error("EM Username Validations Error:" + message);
          }
        }
      },
      email: {
        required,
        email,
        async checkEmail(value) {
          try {
            let result = await call.validateEmail(value);
            if (result.kwargs.isAvailable) {
              return true;
            } else {
              this.$v.user.email.$params.checkEmail = result.kwargs.error;
              return false;
            }
          }
          catch(err) {
            this.$v.user.email.$params.checkEmail = err.kwargs.desc || JSON.stringify(err);
            return false;
          }
        }
      },
      password: {
        required,
        minLength: minLength(8),
        maxLength: maxLength(20),
        regExPass
      },
      confirmPassword: {
        sameAsPassword: sameAs('password')
      },
      currency: {
        name: {
          required
        }
      },
      country: {
        required
      },
      acceptTC: {
        checked: value => value === true
      }
    }
  },
  components:{
    'social-auth': SocialAuth,
    'v-input': Input,
    'multiselect': Multiselect,
    'country-flag': CountryFlag,
    Dropdown,
    PasswordMeter
  },
  methods: {
    ...mapMutations(['UPDATE_AUTH_COMPONENT']),
    registration() {
      this.$v.user.$touch();
      if (this.$v.user.$error) {
        return 0;
      }
      let params = {
        "emailVerificationURL": this.activationUrl,
        "email": this.user.email,
        "password": this.user.password,
        "currency": this.user.currency.code,
        "username": this.user.userName,
        "country": this.user.country,
        "birthDate": `${this.bYear}-${this.bMonth}-${this.bDay}`,
        "userConsents": {
            "termsandconditions": this.user.acceptTC,
            "emailmarketing": false,
            "sms": false,
             "3rdparty": false
        }
      };
      if (!this.isLoading) {
        this.isLoading = true;
        this.accountRegister(params)
          .then((response) => {
            this.$notify({
              group: 'app',
              type:'success',
              text: 'Registration completed successfully. Check your email to activate your account'
            });
            console.log("  ...", response);
            this.UPDATE_AUTH_COMPONENT({name: 'log', isShown: true});
          })
          .catch((err) => {
            const message = err.kwargs.desc || JSON.stringify(err);
            this.$notify({
              group: 'app',
              type:'error',
              text: message
            });
            console.error(`EM Registration Error: ${message}`);
          })
          .finally(() => {
            this.isLoading = false;
          });
      }
    },
    async accountRegister(params) {
      return await call.accountRegister(params);
    },
    updateUserName(val) {
      this.delayTouch(this.$v.user.userName);
      this.user.userName = val;
    },
    updateUserEmail(val) {
      this.delayTouch(this.$v.user.email);
      this.user.email = val;
    },
    updateUserPassword(val) {
      this.delayTouch(this.$v.user.password);
      this.user.password = val;
    },
    updateUserConfirmPassword(val) {
      this.delayTouch(this.$v.user.confirmPassword);
      this.user.confirmPassword = val;
    },
    updateCurrency(val) {
      this.user.currency = val;
    },
    updateUserCountry(val) {
      val
        ? this.user.country = val.code
        : 0;
    },
    updateBirthDate(dateName, e) {
      if (e === null) return 0;
      let val = e;
  
      dateName === 'birthDay' ? this.bDay = val : 0;
      dateName === 'birthMonth' ? this.bMonth = val : 0;
      dateName === 'birthYear' ? this.bYear = val : 0;
    },
    async getCurrencies() {
      try {
        let result = await call.getCurrencies();
        if (result) {
          this.currencies = result.kwargs;
        }
      }
      catch(err) {
        const message = err.kwargs.desc || JSON.stringify(err);
        this.$notify({
          group: 'app',
          type:'error',
          text: message
        });
        console.error(`EM getCurrencies Error: ${message}`);
      }
    },
    async getCountries() {
      try {
        let result = await call.getCountries();
        if (result) {
          this.userIpCountry = result.kwargs.currentIPCountry;
          this.countries = result.kwargs.countries;
        }
      }
      catch(err) {
        const message = err.kwargs.desc || JSON.stringify(err);
        this.$notify({
          group: 'app',
          type:'error',
          text: message
        });
        console.error(`EM getCountries Error: ${message}`);
      }
    },
    async getPasswordPolicy() {
      try {
        let result = await call.getPasswordPolicy();
        if (result) {
          return helpers.regex('alpha', new RegExp(result.kwargs.regularExpression));
        } else {
          return helpers.regex('alpha', /(?=.*\d+)(?=.*[A-Za-z]+).{8,20}/);
        }
      }
      catch(err) {
        const message = err.kwargs.desc || JSON.stringify(err);
        this.$notify({
          group: 'app',
          type:'error',
          text: message
        });
        console.error(`EM getPasswordPolicy Error: ${message}`);
      }
    },
    delayTouch($v) {
      $v.$reset();
      if (touchMap.has($v)) {
        clearTimeout(touchMap.get($v));
      }
      touchMap.set($v, setTimeout($v.$touch, 1000));
    },
  },
  computed: {
    typePassword() {
      return (this.showPass) ? 'text' : 'password';
    },
    daysTab() {
      let arr =[];
      for (let i = 1; i <= 31; i++) {
        arr.push(i.toString().padStart(2, '0'));
      }
      return arr;
    },
    monthTab() {
      let arr =[];
      for (let i = 1; i <= 12; i++) {
        arr.push(i.toString().padStart(2, '0'));
      }
      return arr;
    },
    countryIndex() {
      return (this.countries && this.userIpCountry && !this.user.country)
        ? ~this.countries.map((e) => e.code).indexOf(this.userIpCountry)
          ? this.countries.map((e) => e.code).indexOf(this.userIpCountry)
          : ''
        : this.countries.map((e) => e.code).indexOf(this.user.country);
    },
    firstYear() {
      return (this.countries && this.countryIndex && this.userIpCountry)
        ? new Date().getFullYear() - this.countries[this.countryIndex].legalAge
        : new Date().getFullYear() - 18;
    },
    yearsTab() {
      let arr = [];
      for (let i = this.firstYear; i >= this.firstYear - 100; i--) {
        arr.push(i);
      }
      return arr;
    },
  },
  mounted() {
    this.getCountries();
    this.getCurrencies();
    regExPass = this.getPasswordPolicy();
  }
}
</script>

<style lang="sass" scoped>
.title
  font-family: $mainFont
  font-size: rem(32)
  line-height: 1.2
  margin: rem(70) 0 rem(36)
  text-align: left
.registration
  &__form >
    margin-top: rem(50)
    .input
      margin-top: rem(36)
    .dropdown
      margin-top: rem(36)
    .checkbox
      margin-top: rem(36)
    .btn
      margin-top: rem(30)
    .password-meter
      margin-top: rem(26)
    .birth
      margin-top: rem(20)
      .blackTitle
        text-align: left
        margin-bottom: rem(5)
        color: #636e7e
  .dateRow
    display: flex
    justify-content: space-between
    &__day, &__month, &__year
      width: calc(33% - 5px)
  .countryRow__country
    margin-top: 30px
  .flag
    position: relative
    top: 2px

</style>
