import Vue from 'vue';
import Router from 'vue-router';
import Home from '../views/Home.vue';
import store from '../store/index';

import auth from './middleware/auth';
import middlewarePipeline from './middlewarePipeline';
import blockCreatePass from "./middleware/createPass";

Vue.use(Router);

const router = new Router({
  // base: process.env.BASE_URL,
  mode: (process.env.NODE_ENV === 'development') ? '' : 'history',
  base: '/',
  routes: [
    {
      path: '/',
      name: 'home',
      component: Home,
      children: [
        {
          path: '/auth/:name',
          name: 'auth',
          component: Home,
          meta: {
            middleware: [
              blockCreatePass
            ]
          }
        },
      ]
    },
    {
      path: '/about',
      name: 'about',
      component: () => import(/* webpackChunkName: "about" */ '../views/About.vue')
    },
    {
      path: '/promotions',
      name: 'promotions',
      component: () => import(/* webpackChunkName: "promotions" */ '../views/Promotions.vue')
    },
    {
      path: '/vip',
      name: 'vip',
      component: () => import(/* webpackChunkName: "vip" */ '../views/Vip.vue')
    },
    {
      path: '/activation&key=:id',
      name: 'activation',
      // route level code-splitting
      // this generates a separate chunk (about.[hash].js) for this route
      // which is lazy-loaded when the route is visited.
      component: () => import(/* webpackChunkName: "about" */ '../views/Activation.vue')
    },
    {
      path: '/resetpwd&key=:id',
      name: 'resetpwd',
      component: () => import(/* webpackChunkName: "about" */ '../views/CheckResetPassKey.vue')
    },
    {
      path: '/account/:tab?',
      name: 'account',
      component: () => import(/* webpackChunkName: "account" */ '../views/Account.vue'),
      meta: {
        middleware: [
          auth
        ]
      },
    },
    {
      path: '*',
      name: 'NotFound',
      component: () => import(/* webpackChunkName: "about" */ '../views/404.vue')
    }
  ]
});

router.beforeEach((to, from, next) => {
  if (!to.meta.middleware) {
    return next()
  }
  const middleware = to.meta.middleware;
  const context = {
    to,
    from,
    next,
    store
  };
  return middleware[0]({
    ...context,
    next: middlewarePipeline(context, middleware, 1)
  })
});

export default router;
