<template>
  <div class="create-pass">
    <h2 class="title">Create new password</h2>
    <div class="desc">
      Please, create new password for your account!.
    </div>
  
    <create-password @onSend="createPass" />
    
  </div>
</template>

<script>
import CreatePassword from '@/components/CreatePassword';
import call from '@/services/callService';
import {mapMutations} from 'vuex';

export default {
  name: 'create-pass',
  data() {
    return {
    }
  },
  components:{
    'create-password' : CreatePassword
  },
  methods: {
    ...mapMutations(['UPDATE_AUTH_COMPONENT']),
    async resetPass(params) {
      await call.resetPassword(params);
    },
    createPass(password) {
      const params = {
        "key": this.getKey,
        "password": password
      };
      if (!this.isLoading) {
        this.isLoading = true;
        this.resetPass(params)
          .then((result)=>{
            console.log('New password successfully created', result);
            this.$notify({
              group: 'app',
              type:'success',
              text: 'New password successfully created'
            });
            this.goToLogin();
            this.$router.push('/');
          })
          .catch((err) => {
            const message = err.kwargs.desc || JSON.stringify(err);
            this.$notify({
              group: 'app',
              type:'error',
              text: message
            });
            console.error(`EM Create New Password Error: ${message}`);
          })
          .finally(() => {
            this.isLoading = false;
          });
      }
    },
    goToLogin() {
      this.UPDATE_AUTH_COMPONENT({name: 'log', isShown: true});
    },
  },
  computed: {
    getKey() {
      return this.$route.params.id;
    }
  },
  mounted() {}
}
</script>

<style lang="sass" scoped>
.title
  font-family: $mainFont
  font-size: rem(32)
  line-height: 1.2
  margin: rem(70) 0 rem(18)
  text-align: left
.desc
  color: #636e7e
  font-family: $mainFont
  font-size: 1rem
  font-weight: 500
  line-height: 1.13
  text-align: left
  margin: 0 0 rem(58)
</style>
