import Vue from 'vue';
import App from './App.vue';
import Vuelidate from 'vuelidate';
import router from './router/index';
import store from './store/index';
import Notifications from 'vue-notification';
const VueWamp = require( 'vue-wamp/dist/vue-wamp.cjs');

import VueLoading from 'vue-loading-overlay';
import 'vue-multiselect/dist/vue-multiselect.min.css';
import 'vue-loading-overlay/dist/vue-loading.css';
Vue.config.productionTip = false;

Vue.use(Vuelidate);
Vue.use(Notifications);
Vue.use(VueLoading);
Vue.component('loading', VueLoading);
Vue.use(VueWamp, {
  transports: [{
    'type': 'websocket',
    'url': 'wss://api-stage.everymatrix.com/v2'
  }],
  realm: 'http://www.flipperflip.com',
  max_retries: 3,
  debug: false,
  onopen: function(session, details) {
    console.log('WAMP connected', session, details);
  },
  onclose: function(reason, details) {
    console.log('WAMP closed: ' + reason, details);
  }
});

let vue = new Vue({
  router,
  store,
  render: h => h(App),
  provide: {
    stopSkroll (isOpen) {
      const body = document.body;
      if (isOpen) {
        body.classList.add('modal-open')
      } else {
        body.classList.remove('modal-open')
      }
    }
  }
}).$mount('#app');
