<template>
  <header>

    <div class="container">
      <div class="row">
        <nav class="nav">
          <div class="container">
            <div class="nav__wrapper">
              <div class="nav__logo">

                <!-- logo -->
                <router-link to="/">
                  <img src="@/assets/img/svg/logo.svg" alt="FlipperFlip" v-if="isWhite">
                  <img src="@/assets/img/svg/logo-black.svg" alt="FlipperFlip" v-else>
                </router-link>


                <!-- lang -->
                <div class="nav__lang language-select">
                  <Dropdown class="dropdown" :class="{'dropdown--white' : !isWhite}" :options="langs"
                            v-model="currentLang"
                            @change="updateLang">
                  </Dropdown>
                </div>
              </div>


              <div class="nav__buttons">
                <button class="btn" :class="[isWhite ? 'btn--ghost' : 'btn--ghost-white']" type="button" name="Log"
                        @click="signIn">Sign up
                </button>

              </div>
            </div>
          </div>
        </nav>
      </div>
    </div>

  </header>
</template>

<script>
  import Dropdown from '@/components/Dropdown';
  import Search from '@/components/Search';
  import {mapMutations, mapGetters} from 'vuex';

  export default {
    name: 'Menu',
    data() {
      return {
        search: '',
        currentLang: {
          name: 'En'
        },
        langs: [
          {
            name: 'En'
          },
          {
            name: 'Sk'
          },
          {
            name: 'Ua'
          }
        ]
      }
    },
    components: {
      Dropdown,
      Search
    },
    methods: {
      ...mapMutations(['UPDATE_AUTH_COMPONENT']),
      updateLang(val) {
        this.currentLang = val;
      },
      signIn() {
        this.UPDATE_AUTH_COMPONENT({name: 'log', isShown: true});
      },
      createAccount() {
        this.UPDATE_AUTH_COMPONENT({name: 'reg', isShown: true});
      },
      makeDeposit() {
        this.$router.push({name: 'account', params: {tab: 'deposit-money'}});
      }
    },
    computed: {
      ...mapGetters({
        sessionInfo: 'getSessionInfo',
        balance: 'getProfileBalance',
        profile : 'getProfile'
      }),
      totalBalance() {
        return (this.balance.accounts)
          ? this.balance.accounts.reduce((total, account) => total + account.amount, 0)
          : 'N/A';
      },
      isWhite() {
        let white = 'white';
        let transparent = 'transparent';
        let header_color;
        switch (this.$route.name) {
          case 'home':
            header_color = white;
            break;
          case 'vip':
            header_color = white;
            break;
          case 'auth':
            header_color = transparent;
            break;
          default:
            header_color = transparent;
        }
        return (header_color === white);
      }
    }

  }
</script>

<style lang="sass" scoped>
  .nav
    height: rem(80)
    display: flex
    flex-direction: row
    justify-content: center
    align-items: center
    position: absolute
    width: 100%
    top: 0
    left: 0
    background-color: transparent

    &__wrapper
      display: flex
      flex-direction: row
      justify-content: space-between
      align-items: center

    &__logo
      display: flex
      flex-direction: row
      align-items: center
      justify-content: flex-start
      width: 37%

    &__search
      margin: 0 rem(20) 0 rem(40)

    &__links
      width: 26%

    &__list
      display: flex
      flex-direction: row
      justify-content: center

      li
        padding: 0 rem(10)
        position: relative

      a
        font-family: $mainFont
        font-size: 0.875rem
        color: white

        &.router-link-exact-active
          &:before
            content: ''
            position: absolute
            width: 100%
            height: rem(4)
            background: $colorMain
            border-radius: rem(20)
            top: rem(-31)
            left: 0

    &__buttons
      display: flex
      flex-direction: row
      justify-content: flex-end
      width: 37%

      .btn
        margin: 0 rem(8)

    &--white
      background-color: #F3F4F5

      .nav
        &__list
          a
            color: black

    .user-info
      display: flex
      flex-direction: row
      align-items: center
      margin: 0 rem(16) 0 0
      position: relative
      &__avatar
        border: rem(3) solid #b9bec5
        border-radius: 50%
        margin-right: rem(10)

      &__text
        font-size: 0.875rem
        text-align: left
        color: #636e7e
        font-family: $mainFont
        font-weight: 500

        span
          color: $colorMain
          line-height: 1.29

    .dropdown
      padding: rem(5) 0
    .nav__lang
      margin-left: 20px
  @media screen and (max-width: 768px)
    .language-select
      display: none
</style>
