import Vue from 'vue'
// import store from '@/store/index';

export default {
  sessionStateChange(onSubscribedCallback, onSignInCallback, onSignOutCallback) {
    Vue.Wamp.subscribe('/sessionStateChange', (url, stateChange) => {
      switch (stateChange.code) {
        case 0: // Session is logged-in.
          // store.commit('UPDATE_AUTH_COMPONENT', {isShown: true});
          onSignInCallback();
          break;
        case 1: // Session is expired.
        case 2: // Session is logged off
        case 3: // Session is terminated because another login occurs
        case 5: // Session is terminated because pre-set limitation time is reached.
        case 6: // Session is terminated because self-exclusion is enabled
          console.log(stateChange.code);
          onSignOutCallback(stateChange.desc);
          break;
        default: // Session default
          console.error(`EM Session state change Error unknown code ${stateChange.code}`);
      }
    }).then(() => {
      onSubscribedCallback();
      console.log('On session state change event was subscribed');
    }).catch((err) => {
      console.error(`EM Can't subscribe on session state change. Details: ${err}`);
    });
  },
  depositStatusChanged(onSuccess, onCancel, onError, pid) {
    return Vue.Wamp.subscribe('/deposit/statusChanged', (url, depositStatus) => {
      switch (depositStatus.status) {
        case 'cancel':
          onCancel(depositStatus);
          break;
        case 'error':
          onError(depositStatus);
          break;
        case 'success':
          onSuccess(depositStatus);
          break;
        default: // deposit status default
          console.error(`EM Deposit Status Changed Error unknown status ${depositStatus.status}`);
      }
    }, pid);
  },
  withdrawStatusChanged(onSuccess, onCancel, onError, pid) {
    return Vue.Wamp.subscribe('/withdraw/statusChanged', (url, withdrawStatus) => {
      switch (withdrawStatus.status) {
        case 'cancel':
          onCancel(withdrawStatus);
          break;
        case 'error':
          onError(withdrawStatus);
          break;
        case 'success':
          onSuccess(withdrawStatus);
          break;
        default: // withdraw status default
          console.error(`EM Withdraw Status Changed Error unknown status ${withdrawStatus.status}`);
      }
    }, pid);
  },
  balanceChanged(id) {
    console.log('i am subscrubed ' + JSON.stringify(id));
    return Vue.Wamp.subscribe('/account/balanceChanged', (...args) => {
      console.log(args);
    }, id)
  }
}