export const getUser = state => state.user;
export const getProfile = state => state.profile;
export const getCountries = state => state.countries.countries;
export const getCurrentIPCountry = state => state.countries.currentIPCountry;
export const getLanguages = state => state.languages;
export const getCurrencies = state => state.currencies;
export const getPhonePrefixes = state => state.phonePrefixes;
export const getUserCurrency = state => (state.profile.fields) ? state.profile.fields.currency : '';
export let   getFullCountryName = state => short_name => {
  let match_countries = state.countries.countries.filter((country) => country.code === short_name);
  return match_countries.length ? match_countries[0].name : '';
};
export const getProfileBalance = state => state.gamingAccounts;
