export const UPDATE_USER = (state, payload) => {
	state.user = Object.assign({}, state.user, payload.kwargs);
};

export const UPDATE_PROFILE = (state, payload) => {
  state.profile = Object.assign({}, state.profile, payload.kwargs);
};

export const UPDATE_PROFILE_STATUS = (state, payload) => {
  state.user.isProfileIncomplete = payload.kwargs.isProfileIncomplete;
};

export const UPDATE_COUNTRIES = (state, payload) => {
  state.countries = Object.assign({}, state.countries, payload.kwargs);
};

export const UPDATE_LANGUAGES = (state, payload) => {
  state.languages = Object.assign({}, state.languages, payload.kwargs);
};

export const UPDATE_CURRENCIES = (state, payload) => {
  state.currencies = Object.assign({}, state.currencies, payload.kwargs);
};

export const UPDATE_PHONE_PREFIXES = (state, payload) => {
  state.phonePrefixes = Object.assign({}, state.phonePrefixes, payload.kwargs);
};

export const UPDATE_GAMING_ACCOUNTS = (state, payload) => {
  state.gamingAccounts = Object.assign({}, state.gamingAccounts, payload.kwargs);
};

export const UPDATE_FIELDS = (state, payload) => {
  (payload.validation)
    ? (state.profile[payload.validation])
        ? state.profile.fields[payload.name] = payload.val
        : console.log('   ')
    : state.profile.fields[payload.name] = payload.val;
};

export const UPDATE_BIRTH_DATE = (state, payload) => {
  if (state.profile.isBirthDateUpdatable) {
    state.profile.fields[payload.dateName] = payload.val;
    state.profile.fields.birthDate = `${state.profile.fields.birthYear}-${state.profile.fields.birthMonth.toString().padStart(2, '0')}-${state.profile.fields.birthDay.toString().padStart(2, '0')}`;
  } else {
    console.log('   ');
  }
};

export const RESET_USER = (state) => {
	state.user = {};
  state.profile = {};
  state.countries = {};
  state.languages = {};
  state.currencies = {};
  state.phonePrefixes = {};
  state.gamingAccounts = {};
};


