<template>
  <form class="create-pass__form" @submit.prevent="createPass">
    
    <!-- Loading Overlay -->
    <loading :active.sync="isLoading"
             :is-full-page="false"
             color="#0060f1"
             loader="dots">
    </loading>
    
<!--     pass -->
    <v-input  name="Password"
              :type="typePassword"
              v-model="user.password"
              @change="updateUserPassword"
              :isError="$v.user.password.$error"
    >
      <template slot="optional">
        <button class="btn btn--empty" type="button" name="showPass" @click="showPass = !showPass">
          <img width="20" src="@/assets/img/svg/eye.svg" alt="eye" v-if="showPass">
          <img width="20" src="@/assets/img/svg/hide.svg" alt="eye" v-else>
        </button>
      </template>
      <template slot="error">
        <div class="error" v-if="!$v.user.password.required">Field is required</div>
        <div class="error" v-else-if="!$v.user.password.regExPass">Wrong password format.</div>
        <div class="error" v-else-if="!$v.user.password.minLength || !$v.user.password.maxLength">Password must contain 8-20 symbols.</div>

      </template>
    </v-input>
  
    <!-- password meter -->
    <password-meter :pass="user.password"/>
    
    
    <!-- confirm pass -->
    <v-input  name="Confirm password"
              :type="typePassword"
              v-model="user.confirmPassword"
              @change="updateUserConfirmPassword"
              :isError="$v.user.confirmPassword.$error"
    >
      <template slot="optional">
        <button class="btn btn--empty" type="button" name="showPass" @click="showPass = !showPass">
          <img width="20" src="@/assets/img/svg/eye.svg" alt="eye" v-if="showPass">
          <img width="20" src="@/assets/img/svg/hide.svg" alt="eye" v-else>
        </button>
      </template>
      <template slot="error">
        <div class="error" v-if="!$v.user.confirmPassword.sameAsPassword">Passwords must be identical.</div>
      </template>
    </v-input>
  
    <button class="btn" type="submit" name="Set new password">
      Set new password
    </button>
    
  </form>
</template>

<script>

const touchMap = new WeakMap();
import PasswordMeter from '@/components/Authorization/PasswordMeter';
import Input from '@/components/Input';
import call from '@/services/callService';
import {helpers, required, sameAs, minLength, maxLength} from 'vuelidate/lib/validators';
let regExPass = helpers.regex('alpha', /(?=.*\d+)(?=.*[A-Za-z]+).{8,20}/);

export default {
  name: 'create-password',
  components: {
    'v-input': Input,
    PasswordMeter
  },
  data() {
    return {
      user: {
        password: '',
        confirmPassword: ''
      },
      showPass: false,
      isLoading: false,
    }
  },
  validations: {
    user: {
      password: {
        required,
        minLength: minLength(8),
        maxLength: maxLength(20),
        regExPass
      },
      confirmPassword: {
        sameAsPassword: sameAs('password')
      }
    }
  },
  mounted() {
    regExPass = this.getPasswordPolicy();
  },
  computed: {
    typePassword() {
      return (this.showPass) ? 'text' : 'password';
    },
  },
  methods: {
    async getPasswordPolicy() {
      try {
        let result = await call.getPasswordPolicy();
        if (result) {
          return helpers.regex('alpha', new RegExp(result.kwargs.regularExpression));
        } else {
          return helpers.regex('alpha', /(?=.*\d+)(?=.*[A-Za-z]+).{8,20}/);
        }
      }
      catch(err) {
        const message = err.kwargs.desc || JSON.stringify(err);
        console.error(message);
      }
    },
    delayTouch($v) {
      $v.$reset();
      if (touchMap.has($v)) {
        clearTimeout(touchMap.get($v));
      }
      touchMap.set($v, setTimeout($v.$touch, 1000));
    },
    updateUserPassword(val) {
      this.delayTouch(this.$v.user.password);
      this.user.password = val;
    },
    updateUserConfirmPassword(val) {
      this.delayTouch(this.$v.user.confirmPassword);
      this.user.confirmPassword = val;
    },
    createPass() {
      this.$v.user.password.$touch();
      this.$v.user.confirmPassword.$touch();
      
      if (this.$v.user.password.$error || this.$v.user.confirmPassword.$error ) {
        return 0;
      }
      this.$emit('onSend', this.user.password)
    }
  }
}
</script>

<style lang="sass" scoped>
  .create-pass
    &__form >
      margin-top: rem(50)
      .input
        margin-top: rem(36)
      .input:nth-child(2)
        margin-top: 0
      .btn
        display: block
        margin: rem(30) auto 0 0
      .password-meter
        margin-top: rem(26)
</style>

